import './App.css';
import React, { useState } from 'react';
import Greetings from './Components/Greetings/Greetings';
import LinkHolder from './Components/LinkHolder/LinkHolder';
import PhotoBrowser from './Components/PhotoBrowser/PhotoBrowser';
import Tickets from './Components/Tickets/Tickets';
import Description from './Components/Description/Description';
import Gallery from './Components/Gallery/Gallery';
import MapCropper from './Components/MapCropper/MapCropper';
import Sponsors from './Components/Sponsors/Sponsors';
import Footer from './Components/Footer/Footer';
import YandexMaps from './Components/YandexMaps/YandexMaps';
import Timer from './Components/Timer/Timer';




const App = ()  =>{

  const [accept, setAccepted] = useState(false);




  if (accept === true){
  return (
    <div className="App">
      <Timer/>
      <Greetings/>
      <PhotoBrowser/>
      <Description/>
      <Gallery/>
      <MapCropper/>
      <Footer/>
    </div>
  );

  }else{
    return (
      <div className="App">
          <div className="wrapper">
            <LinkHolder setAccepted = {setAccepted}/>
          </div>
      </div>
    );
  }
}

export default App;
