import style from './style.module.css';
import { useMediaQuery } from 'react-responsive';
import { YMaps, Map, Placemark } from "react-yandex-maps";


const YandexMaps = (props) =>{


    
const mapState = { center: [55.666228, 37.463179], zoom: 16 };
    const state = { width: '100%', height: '400px'};
    const { width, height} = state;


    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
    if (isDesktopOrLaptop === true){
    return (
        <div>
            <div className = {style.desktopWrapper}>
                <YMaps>
                    <div className = {style.yandexMapWrapper}>
                        <Map state={mapState} width={width} height={height} >
                            <Placemark geometry={[55.666228, 37.463179]}>
                            </Placemark>
                        </Map>
                    </div>
                </YMaps>
            </div>
        </div>
    );
    }else{
        return(
<div>
            <div className = {style.mobileWrapper}>
                <YMaps>
                    <div className = {style.yandexMapWrapper}>
                        <Map state={mapState} width={width} height={height} >
                            <Placemark geometry={[55.666228, 37.463179]}>
                            </Placemark>
                        </Map>
                    </div>
                </YMaps>
            </div>
        </div>
        );
    }
}

export default YandexMaps;