import React from 'react';
import style from './style.module.css'
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';




const LinkHolder = (props) =>{


    const preventDefault = (event) => event.preventDefault();




    return(
        <div className = {style.wrapper}>

            <div className = {style.buttonWrapper}>
              <div className = {style.alertTextWrapper}>
                <h2 className = {style.alertText}>Это не финальный вариант сайта.</h2>
                <h2 className = {style.alertText}>Сайт находится в разработке.</h2>
              </div>
              <button className = {style.okButton} onClick = {() => props.setAccepted(true)}>
                <h2 className = {style.okButtonText}>Далее</h2>
              </button>
            </div>

        </div>
    )
}

export default LinkHolder;