
import style from './style.module.css';
import { useMediaQuery } from 'react-responsive'
import { Fade } from 'react-slideshow-image';
import StyledText from '../StyledText/StyledText';
import SmallStyledText from '../SmallStyledText/SmallStyledText';
import LinkHolder from '../LinkHolder/LinkHolder';
import Photo1 from './HomePhoto.jpg';
import Photo2 from './HomePhoto2.jpg';
import Photo3 from './HomePhoto3.jpg';
import Photo4 from './HomePhoto4.jpg';
import DarkPentagon from './DarkPentagon.png';
import PurpleCircle from './PurpleCircle.png';
import YellowCircle from './YellowCircle.png';
import LightSquare from './LightSquare.png';

const fadeProperties = {
    autoplay: true,
    arrows: false,
  };



  
const Greetings = (props) =>{

    

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
    if (isDesktopOrLaptop === true){
    return (
        <div>

                <div className = {style.desktopWrapper}>
                 

                    <div className = {style.geometryDecoration}>
                        <img className = {style.darkPentagon} src = {DarkPentagon}/>
                    </div>
                    <div className = {style.geometryDecoration2}>
                        <img className = {style.darkPentagon2} src = {PurpleCircle}/>
                    </div>
                    <div className = {style.geometryDecoration3}>
                        <img className = {style.darkPentagon3} src = {YellowCircle}/>
                    </div>
                    <div className = {style.geometryDecoration4}>
                        <img className = {style.darkPentagon4} src = {LightSquare}/>
                    </div>
                    <div className = {style.buyTicket}>
                        <div className = {style.buyTicket}>
                            <p><a href = '#' className = {style.buyTicketLink} >купить билет</a></p>
                        </div>
                    </div>
                    <div className = {style.carouselWrapper}>                 
                        <Fade {...fadeProperties}>
                            <div>
                                <img src={Photo1} className = {style.carouselPhoto}/>
                            </div>
                            <div >
                                <img src={Photo2} className = {style.carouselPhoto}/>
                            </div>
                            <div >
                                <img src={Photo3} className = {style.carouselPhoto}/>
                            </div>
                            <div >
                                <img src={Photo4} className = {style.carouselPhoto}/>
                            </div>
                        </Fade>
                    </div>
                    <div className = {style.mainTextWrapper}>
                    <div className = {style.styledTextWrapper}>
                            <StyledText text = {'TRIPLE F'}/>
                        </div>
                        <div className = {style.styledTextWrapper2}>
                            <SmallStyledText text = {'ГИК-ЯРМАРКА'}/>
                        </div>
                    </div>
                    <div className = {style.buttonWrapper}>
                        <button className = {style.ticketButton}>
                            КУПИТЬ БИЛЕТ
                        </button>
                    </div>
                </div>
        </div>
    );
    }else{
        return(
        <div>
                <div className = {style.mobileWrapper}>
                    <div className = {style.geometryDecorationMobile}>
                        <img className = {style.darkPentagon} src = {DarkPentagon}/>
                    </div>
                    <div className = {style.buyTicket}>
                            <p><a href = '#' className = {style.buyTicketLink} >купить билет</a></p>
                    </div>
                    <div className = {style.carouselWrapperMobile}>
                        <Fade {...fadeProperties}>
                            <div>
                                <img src={Photo1} className = {style.carouselPhotoMobile}/>
                            </div>
                            <div >
                                <img src={Photo2} className = {style.carouselPhotoMobile}/>
                            </div>
                            <div >
                                <img src={Photo3} className = {style.carouselPhotoMobile}/>
                            </div>
                            <div >
                                <img src={Photo4} className = {style.carouselPhotoMobile}/>
                            </div>
                        </Fade>
                    </div>
                    <div className = {style.mainTextWrapper}>
                        <div className = {style.styledTextWrapper}>
                            <StyledText text = {'TRIPLE F'}/>
                        </div>
                        <div className = {style.styledTextWrapper2}>
                            <SmallStyledText text = {'ГИК-ЯРМАРКА'}/>
                        </div>
                    </div>
                    <div className = {style.buttonWrapper}>
                        <button className = {style.ticketButton}>
                            КУПИТЬ БИЛЕТ
                        </button>
                    </div>
                </div>
        </div>
        );
    }
}

export default Greetings;