import React from 'react';
import style from './style.module.css';
import QuickLocation from '../QuickLocation/QuickLocation';
import DarkStar from './DarkStar.png';
import { useMediaQuery } from 'react-responsive'

const MapCropper = () =>{


    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
    
    if (isDesktopOrLaptop === true){
        return(
            <div className = {style.wrapper}>
                <div className = {style.geometryDecoration}>
                    <img className = {style.darkStar} src = {DarkStar} alt = 'Background  decoration'/>
                </div>
                <QuickLocation/>
            </div>
        )
    }else{
        return(
            <div className = {style.wrapper}>
                <div className = {style.geometryDecorationMobile}>
                    <img className = {style.darkStarMobile} src = {DarkStar} alt = 'Background  decoration'/>
                </div>
                <QuickLocation/>
            </div>
        )
    }
}

export default MapCropper;